<template>
	<div id="public-footer">
		<transition name="loading">
			<iloading v-if="showPreLoading"></iloading>
		</transition>
		<template v-if="menufooter && menufooter.data">
			<van-tabbar v-model="active" v-if="menufooter.params.navstyle == 0">
				<van-tabbar-item :to="util.getUrl({path: item.link})" v-for="(item, index) in menufooter.data" :key="index">
					<div slot="icon" slot-scope="props" class="icon font-20" :class="item.icon" :style="{color:  util.isMenuActive(item.link) ? menufooter.css.iconColorActive : menufooter.css.iconColor}"></div>
					<span slot-scope="props" :style="{color: util.isMenuActive(item.link) ? menufooter.css.textColorActive : menufooter.css.textColor}">{{item.text}}</span>
				</van-tabbar-item>
			</van-tabbar>
			<ul class="tabbar-img van-hairline--top" v-else-if="menufooter.params.navstyle == 1">
				<router-link tag="li" :to="util.getUrl({path: item.link})" class="tabbar-img-item" v-for="(item, index) in menufooter.data" :key="index">
					<img :src="util.tomedia(item.img)" alt="">
				</router-link>
			</ul>
		</template>
	</div>
</template>

<script>
	export default {
		name: 'PublicFooter',
		props: {
			showPreLoading: {
				type: Boolean || Number
			},
			menufooter: {
				type: Object,
				default() {
					return {
						params: {
							navstyle: 0,
						},
						css: {
							iconColor: '#000',
							iconColorActive: '#0EC3B3',
							textColor: '#000',
							textColorActive: '#0EC3B3'
						},
						data: {
							takeout: {
								link: '/pages/order/takeout',
								icon: 'icon-order',
								text: '外卖'
							},
							paotui: {
								link: '/pages/paotui/index',
								icon: 'icon-i-activitymonitoring',
								text: '跑腿'
							},
							member: {
								link: '/pages/member/mine',
								icon: 'icon-mine',
								text: '我的'
							}
						},
					}
				}
			},
		},
		data() {
			return {
				active: 0
			}
		}
	}
</script>

<style>
	#public-footer{
		position: absolute;
		z-index: 100;
	}
	#public-footer .van-icon{
		font-size: 20px;
	}
	#public-footer .van-tabbar-item--active {
	    color: #0EC3B3;
	}
	#public-footer .tabbar-img{
		position: fixed;
		right: 0;
		bottom: 0;
		left: 0;
		height: 50px;
		background-color: #fff;
		display: flex;
		align-item: center;
		justify-content: center;
		flex-flow: row nowrap;
	}
	#public-footer .tabbar-img .tabbar-img-item{
		width: 100%;
		height: 100%;
		text-align: center;
	}
	#public-footer .tabbar-img .tabbar-img-item img{
		width: 36px;
		height: 36px;
		margin-top: 7px;
	}
</style>



